<template>
  <div v-show="!moment">
    <label class="r-28-gray" @click="updateShow">Domina tu ira</label>
    <br><br>
    <div class="audio-player">
      <canvas ref="canvasRef" width="256" height="200" class="visualizer"></canvas>

      <div class="song-info">
        <div class="d-flex justify-content-center pb-3">
          <!-- <div class="d-flex justify-content-center" style="align-items: center;">
            <h3 class="margin-auto">{{ songTitles[currentIndex] }}</h3> 
          </div> -->
          <div>
            <img v-show="!isFavorite" src="../assets/heart-gray.png" class="m-auto" style="width: 55px;"
              @click="AddFavorite()" data-bs-toggle="modal" data-bs-target="#modalFavorite">
            <img v-show="isFavorite" src="../assets/heart.png" class="m-auto" style="width: 55px;"
              @click="RemoveFavorite()" data-bs-toggle="modal" data-bs-target="#modalFavorite">
          </div>
        </div>
        <div class="time">
          {{ formatTime(currentTime) }} / {{ formatTime(duration) }}

        </div>
        <input type="range" min="0" :max="duration" v-model="currentTime" @input="seekAudio" />
      </div>
      <div class="d-flex py-5">
        <img src="../assets/Atras.png" class="m-auto" @click="prevTrack">
        <img :src="isPlaying ? pauseIcon : playIcon" style="height: 100%; width: fit-content; margin: auto;"
          @click="togglePlay">
        <img src="../assets/Siguiente.png" class="m-auto" @click="nextTrack">
      </div>
    </div>
    <!-- <br>
    <div class="interseccion-large my-4"></div> -->
    <!-- <p class="m-10-gray text-start px-3">
      El fracaso, la tristeza y el arrepentimiento son los compañeros de viaje de la ira…
      <br><br>
      Es más fácil excluirla y no admitirla que gobernarla…
      <br><br>
      Debes sofocar sus primeros impulsos…
      <br><br>
      Al poseerte ya no puedes contenerla…
      <br><br>
      Séneca
      <br>
      Sobre la ira
    </p>
    <div class="interseccion-large my-4"></div> -->
    <p class="r-14-gray pb-5">
      Music by CalmWaves
      <br><br>
      Arr. by Angerpulse
    </p>
    <label class="b-40-gray bg-white px-5">Guía</label>
    <div class="square-info pt-5" style="margin-top: -30px;">
      <p class="m-16-gray px-4 py-4">
        Cuando escuches el primer UDU (sonido de percusión) inhala despacio y profundo por la nariz.
        <br><br>
        Sin interrumpir la inhalación hasta escuchar el siguiente UDU.
        <br><br>
        Ahora con calma exhala suave y lento por la boca.
        <br><br>
        Sin interrumpir la exhalación hasta escuchar el siguiente UDU.
        <br><br>
        Sigue alternando inhalación y exhalación cada vez que escuches UDU.
      </p>
    </div>
  </div>
  <div v-show="moment">
    <div>
      <div>
        <label class="sb-20-black pb-4">¿En qué momento estabas?</label>
        <ul class="ps-0">
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-m" class="form-check-input" type="radio" name="plan" value="" aria-label="...">
              <label for="plan-m" class="form-check-label">1) Antes de actividades previstas, planeadas o
                rutinarias. </label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-s" class="form-check-input" type="radio" name="plan" value="" aria-label="...">
              <label for="plan-s" class="form-check-label">2) Cavilaciones y sobrepensamiento. Encerrado en tu
                mente.</label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-a" class="form-check-input" type="radio" name="plan" value="" aria-label="...">
              <label for="plan-a" class="form-check-label">3) Transiciones entre situaciones, actividades,
                lugares. </label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-4" class="form-check-input" type="radio" name="plan" value="" aria-label="...">
              <label for="plan-4" class="form-check-label">4) Indignación o irritación por palabras o acciones
                de alguien más.</label>
            </div>
          </li>
          <li class="list-group-item text-start r-16-gray py-2">
            <div class="ps-5 form-check">
              <input id="plan-5" class="form-check-input" type="radio" name="plan" value="" aria-label="...">
              <label for="plan-5" class="form-check-label">5) Después de hablar/actuar de manera emocional, impulsiva e
                incorrecta.</label>
            </div>
          </li>

        </ul>
        <div class="d-flex justify-content-center pt-3 pb-5">
          <div class="px-3">
            <button type="button" class="btn-gray-white" @click="SetMoment()">
              Omitir
            </button>
          </div>
          <div class="px-3">
            <button type="button" class="btn-black border-0" @click="SetMoment()">
              Guardar
            </button>
          </div>
        </div>
      </div>
      <div>
        <label class="sb-20-black pb-5">¿Por qué indicar el momento?</label>
        <p class="r-16-gray text-center px-3 pb-5">
          Distinguir y registrar el “momento” en el que estabas cuando decidiste hacer un audioejercicio(s) te
          ayudará a:
          <br><br>
          - Entender cuándo debes estar más atento para regular tus emociones pre-ira y contener tus principales
          detonantes de ira.
          <br><br>
          - Desarrollar una mayor capacidad de prevención y autocontrol.
          <br><br>
          Si decides no indicar el momento, solo pulsa el botón “omitir” cuando aparezca la pantalla de registro.
        </p>
      </div>
    </div>
  </div>


  <!-- Modal -->
  <div class="modal fade" id="modalFavorite" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-body px-3">
          <div class="p-4 d-flex justify-content-center">
            <label v-show="!isFavoriteQuestion" class="form-label r-24-gray">Se agregó a tu audioteca de favoritos</label>
            <label v-show="isFavoriteQuestion" class="form-label r-24-gray">¿Deseas remover el audio de tus favoritos?</label>
          </div>
          <div class="d-flex justify-content-between pb-3" v-if="isFavoriteQuestion">
            <div>
              <button type="button" class="btn-gray" data-bs-dismiss="modal">
                Cancelar
              </button>
            </div>
            <div>
              <button type="button" class="btn-black" @click="AddFavorite()" data-bs-dismiss="modal">
                Aceptar
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <br><br>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'
import * as bootstrap from 'bootstrap'

let modalElement
let closeTimeout

onMounted(() => {
  modalElement = document.getElementById('modalFavorite')

  // Cuando el modal se muestre, programa su cierre
  modalElement.addEventListener('shown.bs.modal', () => {
    clearTimeout(closeTimeout) // evita múltiples timers
    if (!isFavoriteQuestion.value){
          closeTimeout = setTimeout(() => {
      const modalInstance = bootstrap.Modal.getInstance(modalElement)
      if (modalInstance) {
        modalInstance.hide()
      }
    }, 1000) // 1 segundo
    }
  })
})

onBeforeUnmount(() => {
  if (modalElement) {
    modalElement.removeEventListener('shown.bs.modal', () => {})
  }
  clearTimeout(closeTimeout)
})

const audios = [
  new URL('@/assets/audios/Angerpulse_Audioejercicio_2.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_6.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_7.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_8.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_9.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_11.mp3', import.meta.url).href,
  new URL('@/assets/audios/Angerpulse_Audioejercicio_21.mp3', import.meta.url).href
]

// const songTitles = [
//   'AP 1',
//   'AP 2',
//   'Ap 3',
//   'AP 4',
//   'AP 5',
//   'Ap 6',
//   'Ap 7'
// ]

const moment = ref(false)
const playIcon = new URL('@/assets/Play.png', import.meta.url).href
const pauseIcon = new URL('@/assets/Pausa.png', import.meta.url).href

const currentIndex = ref(0)
const isPlaying = ref(false)
const isShuffle = ref(true)
const currentTime = ref(0)
const duration = ref(0)
const audio = new Audio()
const isFavorite = ref(false)
const isFavoriteQuestion = ref(false)

const canvasRef = ref(null)
let audioContext, analyser, dataArray, source, animationId
let shuffledIndices = []

const hasShownMessage = ref(false)
let playbackInterval = null
const playbackSeconds = ref(0)

function SetMoment() {
  moment.value = false
}

function AddFavorite() {
  isFavorite.value = !isFavorite.value
  isFavoriteQuestion.value = false
}

function RemoveFavorite() {
  isFavoriteQuestion.value = true
}

function setupVisualizer() {
  audioContext = new (window.AudioContext || window.webkitAudioContext)()
  analyser = audioContext.createAnalyser()
  analyser.fftSize = 64
  const bufferLength = analyser.frequencyBinCount
  dataArray = new Uint8Array(bufferLength)

  source = audioContext.createMediaElementSource(audio)
  source.connect(analyser)
  analyser.connect(audioContext.destination)

  drawVisualizer()

  if (audioContext.state === 'suspended') {
    const resume = () => {
      audioContext.resume()
      document.removeEventListener('touchstart', resume)
    }
    document.addEventListener('touchstart', resume, { once: true })
  }
}

function drawVisualizer() {
  const canvas = canvasRef.value
  const ctx = canvas.getContext('2d')
  const width = canvas.width
  const height = canvas.height
  const centerY = height / 2

  function draw() {
    animationId = requestAnimationFrame(draw)
    analyser.getByteFrequencyData(dataArray)

    ctx.clearRect(0, 0, width, height)

    const barWidth = width / dataArray.length
    let x = 0

    for (let i = 0; i < dataArray.length; i++) {

      const value = dataArray[i]
      const barHeight = value / 2
      // ctx.fillStyle = '#aaa'
      const gradient = ctx.createLinearGradient(0, centerY - barHeight, 0, centerY + barHeight)
      gradient.addColorStop(0, '#00f')
      gradient.addColorStop(1, '#0ff')
      ctx.fillStyle = gradient
      ctx.fillRect(x, centerY - barHeight, barWidth, barHeight)
      ctx.fillRect(x, centerY, barWidth, barHeight)
      x += barWidth + 1
    }
  }

  draw()
}

function shuffleArray(length) {
  const indices = [...Array(length).keys()]
  for (let i = indices.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1))
      ;[indices[i], indices[j]] = [indices[j], indices[i]]
  }
  return indices
}

function loadTrack(index) {
  audio.src = audios[index]
  audio.load()
  playbackSeconds.value = 0
  stopPlaybackTimer()
  audio.play().then(() => {
    isPlaying.value = true
    startPlaybackTimer()
  }).catch(err => {
    console.warn('iOS puede haber bloqueado la reproducción:', err)
  })
}

function togglePlay() {
  if (audio.paused) {
    startPlaybackTimer()
    audio.play()
    isPlaying.value = true
  } else {
    stopPlaybackTimer()
    audio.pause()
    isPlaying.value = false
    if (playbackSeconds.value >= 110 || (playbackSeconds.value >= 107 && currentTime.value >= 110) ) {
      moment.value = true;
      hasShownMessage.value = true
      playbackSeconds.value = 0
    }
    console.log(playbackSeconds.value);
    console.log(currentTime.value);
  }
}

function startPlaybackTimer() {
  if (playbackInterval) return // Evita múltiples intervalos

  playbackInterval = setInterval(() => {
    playbackSeconds.value++
  }, 1000)
}

function stopPlaybackTimer() {
  clearInterval(playbackInterval)
  playbackInterval = null
}

function nextTrack() {
  if (isShuffle.value) {
    if (shuffledIndices.length === 0) {
      shuffledIndices = shuffleArray(audios.length)
    }
    currentIndex.value = shuffledIndices.shift()
  } else {
    currentIndex.value = (currentIndex.value + 1) % audios.length
  }
  loadTrack(currentIndex.value)
}

function prevTrack() {
  if (isShuffle.value) {
    if (shuffledIndices.length === 0) {
      shuffledIndices = shuffleArray(audios.length)
    }
    currentIndex.value = shuffledIndices.shift()
  } else {
    currentIndex.value = (currentIndex.value - 1 + audios.length) % audios.length
  }
  loadTrack(currentIndex.value)
}

// function toggleShuffle() {
//   isShuffle.value = !isShuffle.value
//   if (isShuffle.value) {
//     shuffledIndices = shuffleArray(audios.length)
//     if (shuffledIndices.includes(currentIndex.value)) {
//       shuffledIndices = shuffledIndices.filter(i => i !== currentIndex.value)
//     }
//   } else {
//     shuffledIndices = []
//   }
// }

function seekAudio() {
  audio.currentTime = currentTime.value
}

audio.addEventListener('timeupdate', () => {
  currentTime.value = audio.currentTime
  duration.value = audio.duration || 0
})

audio.addEventListener('ended', () => {
  nextTrack()
})

function formatTime(time) {
  const minutes = Math.floor(time / 60)
  const seconds = Math.floor(time % 60).toString().padStart(2, '0')
  return `${minutes}:${seconds}`
}

onMounted(() => {
  setupVisualizer()
  loadTrack(currentIndex.value)
})

onBeforeUnmount(() => {
  cancelAnimationFrame(animationId)
  audio.pause()
  audio.src = ''
  if (audioContext) audioContext.close()
})
</script>

