<style scoped>
.audio-player {
  text-align: center;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  color: #555;
}

.visualizer {
  display: block;
  margin: 1rem auto;
  background-color: white;
}

.song-info {
  margin: 1rem 0;
}

.song-info h3 {
  margin: 0;
}

.song-info .time {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.song-info input[type=range] {
  width: 100%;
}

.controls button {
  background: none;
  border: none;
  margin: 0 1rem;
  cursor: pointer;
  color: white;
}

.controls img {
  width: 40px;
  height: 40px;
}

